import React, { Component } from "react";
import { connect } from "react-redux";
import Link from 'next/link';
import SearchForm from '../search/searchForm';
import styled from 'styled-components';
import OutsideClickHandler from 'react-outside-click-handler';
import { BREAKPOINT_LG,NAVBAR_BACKGROUND,NAV_LIST_COLOR,HEADER_BACKGROUND,SEARCH_BACKGROUND,NAVBAR_PADDING,NAVBAR_SCROLL_BACKGROUND,HEADER_COLOR,BLUE_COLOR,DARK_BORDER_COLOR,BORDER_COLOR_ACTIVE,GREEN_COLOR,LIGHT_BACKGROUND,MAX_WIDTH,BORDER_COLOR,SECONDARY_FONT_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM} from '../types';
const Dropdown = styled.div`
     position: absolute;
    background:${SEARCH_BACKGROUND};
    border-radius: 5px;
    color: #fff;

    &:before {
      content: "";
    right: 100%;
    bottom: 100%;
    position: absolute;
    margin-right: -33px;
    position: absolute;
    width: 0;
    height: 0;
    border: 7px solid transparent;
    border-top-width: 0;
    border-bottom-color:${SEARCH_BACKGROUND};
    }
    transition: all .25s cubic-bezier(.24,.22,.015,1.56),opacity .1s ease-in-out;
    top: 60px;
    max-width: 160px;
    opacity: 0;
    visibility:hidden;
    transform: scale(0.8) translateY(-10px);
    &.active {
          box-shadow: 0 1px 3px 0 rgba(0,0,0,0.08), 0 0 5PX 1px rgba(0,0,0,0.04);
    opacity: 1;visibility:visible;

    transform: scale(1) translateY(0);
}
    ul {
    list-style: none;
    display: block;
    text-align: left;
    
    padding: 12px 15px!important;
    li {
    display: block;
    font-size: 13px!important;
    line-height: 1.6!important;
    min-width: 130px;
    a{
      line-height:30px!important;
          height: auto!important;
    }

}
}
`;
const Navbar = styled.div`
  left: 0;
  right: 0;
 position: fixed;
 background:${NAVBAR_BACKGROUND};
  z-index: 99996 ;
  transition: background 200ms cubic-bezier(0.4, 0, 0.2, 1);
    top: 0;
  &.scroll {
      
    background: ${NAVBAR_SCROLL_BACKGROUND};
    box-shadow: 0 0px 1px rgba(0,0,0,0.2);
    ul{
      padding: ${NAVBAR_PADDING}!important;
      li{
        &.boxicon_brand{
    overflow: hidden;
    .brand{
      width:160px;
    svg {
    width: 100%;
}
    }
        }
       
      }
    }
}

      
ul li a.active{
          
        }
        ul li a:hover{
          color:
         #b06c4a;
        }
    

  .navbar-max{  max-width: ${MAX_WIDTH};
  margin: 0px auto;
    ul{
        &.mounted{
        li{
            a{
                transform:translateY(0);
                opacity:1;
            }
        }
    }
          transition: 0.15s;
          margin: 0;
    list-style: none;
    padding: 7px 25px;
    @media screen and (max-width: ${BREAKPOINT_LG}) {padding: 12px 20px!important;}
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    &.mobile-menu-cont {
      list-style: none;
      position: fixed;

     overflow-y: auto;
      
      display: block;
        li a {
            font-size: 27px !important;
    padding: 10px 20px !important;
       display: block;
      color:#a4a4a4!important;
      &.active,&:hover{
        color:#fff!important;
      }
      }
      background: #313133;
      left: 0;
      right: 0px;
      top: 62px;
      animation-duration: 0.25s;
      bottom: 0px;
      padding: 30px;
      border-bottom: 1px solid rgba(0,0,0,0.05);
      border-top: 1px solid rgba(0,0,0,0.05);
      transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    li{
      input {
          height: 36px!important;
          line-height: 36px!important;
          font-size: 17px!important;
          color:#bbb!important;
      }
      &.mobile-menu {
          i {
            font-size: 1.55rem;
            vertical-align: middle;
            color: ${NAV_LIST_COLOR};
            &:hover{
              color: #b06c4a;
            }
          }
          order: 3;
          display: none;
           @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: inline-block;
            padding-right: 0px !important;
            width: 40px;
            height: 30px;
          }
        }
      &.nav-search {
        flex: 1;
         @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: none !important;
        }
      }
      &.nav-links {
        display: flex;
          text-align: right;
          padding: 5px 0;
          order: 3;
             
         @media screen and (max-width: ${BREAKPOINT_LG}) {
            display: none !important;
        }
      }
      margin: 0px;
      line-height: 20px;
      font-size: 14px;
      padding: 0px;
      &.boxicon_brand { a{padding:0px;}

         @media screen and (max-width: ${BREAKPOINT_LG}) {
          flex: 1;
          height: 38px;
        }
        a{

          &:hover{
              background:transparent!important;
            }
          @media screen and (max-width: ${BREAKPOINT_LG}) {
          
          padding:0px;
        }
        }
    
      }

      
      .search-sticky {
        opacity: 0;
        background: transparent;
        padding-bottom: 0px;
        padding: 0px 15px;
        transition: 0.15s;
            transform: translateY(-10px);
        visibility: hidden;
        &.nav-active {
          opacity: 1;
              transform: translateY(0px);
          visibility: visible;
        }
      }
      .search-cont {
        box-shadow:none!important;
    margin: 4px 0!important;
        max-width: none;
            margin-right: 10px!important;
      &.active input{
        color:#111!important;
      }
        i {
            font-size: 24px!important;
            line-height: 30px;
            padding: 0px 12px!important;
        }
        i.bx-x {
          font-size: 16px !important;
          padding: 5px !important;
          height: 16px !important;
          line-height: 16px !important;
          color: #aab0bc;
          border-radius: 50%;
          cursor: pointer;
          margin-right: 7px;
        }
        padding: 2px;
        margin: 0px;    border-radius: 0px;
        background: ${LIGHT_BACKGROUND};
        border:1px solid ${LIGHT_BACKGROUND};
        &.active {

          
        box-shadow: 0 0px 0 2px #f9f9f9 !important;
          background: #fff;
        }
      }
      a{
         &.show-dropdown {
      cursor: pointer;
}
            font-weight: 400;
            border-radius:2px;
        &.mobile-menu-button {
          line-height: 24px !important;
          height: 24px !important;
              border-radius: 50%;
        padding: 0px 12px;
        }
        &:hover {
          color: #b06c4a;
              
        }
        i {
          font-size: 24px;
         
          line-height: 24px;
    height: 24px;
          vertical-align: middle;
        }

        .brand {
            height: 50px;
           svg {
    width: 100%; height: 42px;
}
    width: 160px;
    margin: 1px 0;
 margin-right: 10px;
 transition:0.15s;
         &:hover{
          opacity:0.9;
         }
        
         
        }
        padding: 0px 15px;
        text-decoration: none;
        color: ${NAV_LIST_COLOR};
        height: 36px ;
        position: relative;
        line-height: 36px ;
        display: inline-block;
        transition: 0.15s;
        font-size: 14px;
        transform: translateY(20px);
        opacity:0;
        

      
      }
    }
  }}
`;

class NavigationBar extends Component {
   constructor(props) {
        super(props);
        this.state = {
            showMobile: false,
            showGum: false,
            showDropdown: false,
            is_Mounted:false,
        };

        this.handleMenuClick = this.handleMenuClick.bind(this);
        this.handleMenuLinkClick = this.handleMenuLinkClick.bind(this);
      this.handleNavbarScroll = this.handleNavbarScroll.bind(this);
        this.handleDropdown = this.handleDropdown.bind(this);
     
    }
   
    handleNavbarScroll(){
   var header = document.querySelector(".navbar-cont");
   
        var fixed_class = "scroll";

        if (window.pageYOffset >50) {
            header.classList.add(fixed_class);

        } else {
            header.classList.remove(fixed_class);
        }
    }
    handleMenuLinkClick(){
      this.setState({showMobile:false});
    }
    handleDropdown(e){
      e.preventDefault();
      this.setState({showDropdown:!this.state.showDropdown});
    }
    handleMenuClick(e){
      e.preventDefault();
      this.setState({showMobile:!this.state.showMobile});
    }
    componentDidMount() {
        setTimeout(
            function() {
              this.setState({is_Mounted:true})
        
            }
            .bind(this),
            10
        );
        window.addEventListener("scroll", this.handleNavbarScroll);
 
    }
    componentWillUnmount() {
       var header = document.querySelector(".navbar-cont");
       header.classList.remove("scroll");
        window.removeEventListener("scroll", this.handleNavbarScroll);
    }
    componentWillMount () {
       
    }
    render() {

        return (
            <Navbar className="navbar-cont" >
              <div className="navbar-max">
                <ul className={this.state.is_Mounted?"navbar mounted":"navbar"} role="navigation">
                  <li className="boxicon_brand">
            <Link href="/">
           <a>
            <div className="brand sprite" ><svg xmlns="http://www.w3.org/2000/svg" width="117" height="23" viewBox="0 4.5 117 23"><path fill="#313133" d="M0 10L0 21.5 11 27.5 11 15.5 22 10 10.7 4.5z"/><g fill="#313133"><path d="M43.4 17.75c-.5-.6-1.1-1-1.8-1.2.7-.2 1.2-.5 1.6-1 .4-.5.6-1.1.6-1.899 0-1-.4-1.9-1.1-2.4-.7-.5-1.7-.8-3-.8H34v12.6h5.9c1.4 0 2.4-.3 3.1-.899.7-.601 1.1-1.4 1.1-2.5C44.1 18.95 43.8 18.35 43.4 17.75zM37 12.85h2c1.1 0 1.6.5 1.6 1.4s-.5 1.4-1.6 1.4h-2V12.85zM39.2 20.55H37v-2.8h2.2c.6 0 1 .1 1.3.4.3.3.4.6.4 1.1C40.9 20.15 40.3 20.65 39.2 20.55zM54.7 15.45c-.4-.8-1-1.4-1.8-1.8-.8-.4-1.601-.601-2.6-.601-1 0-1.9.2-2.7.601-.7.399-1.4 1-1.8 1.8-.5.8-.7 1.7-.7 2.7s.3 1.899.7 2.699c.4.801 1 1.4 1.8 1.801.8.399 1.8.6 2.7.6s1.8-.2 2.6-.6c.7-.4 1.399-1 1.8-1.801.5-.8.7-1.699.7-2.699S55.1 16.25 54.7 15.45zM51.799 19.85c-.399.4-.899.601-1.5.601-.6 0-1.1-.2-1.5-.601-.4-.399-.6-1-.6-1.8s.2-1.399.6-1.8c.4-.4.9-.6 1.5-.6.6 0 1.1.199 1.5.6.4.4.601 1 .601 1.8S52.2 19.45 51.799 19.85zM66.5 13.05L63.199 13.05 61.5 15.85 59.5 13.05 56.1 13.05 59.5 17.85 56.2 23.05 59.5 23.05 61.199 20.25 63.199 23.05 66.6 23.05 63.1 18.15zM69.299 8.75c-.5 0-1 .2-1.299.5-.301.3-.5.6-.5 1.1 0 .4.199.801.5 1.101.299.3.799.5 1.299.5s1-.2 1.301-.5.5-.601.5-1.101c0-.399-.199-.8-.5-1.1S69.799 8.75 69.299 8.75zM67.799 13.15H70.799V23.049H67.799zM77.4 15.55c.398 0 .799.2 1.1.4.299.2.5.6.6 1h3.199c-.199-1.2-.699-2.2-1.6-2.9s-2-1-3.199-1.3c-1 0-1.801.2-2.6.6-.801.4-1.4 1-1.801 1.801-.4.699-.6 1.699-.6 2.699s.199 1.9.6 2.7c.5.8 1 1.4 1.801 1.8.799.4 1.6.601 2.6.601 1.299 0 2.299-.4 3.199-1.101.801-.699 1.4-1.699 1.6-2.899H79.1c-.1.399-.301.8-.6 1-.301.3-.6.399-1.1.399s-1-.199-1.301-.6-.5-1-.5-1.8.199-1.4.5-1.8C76.4 15.75 76.9 15.55 77.4 15.55L77.4 15.55zM93 15.45c-.4-.8-1-1.4-1.801-1.8-.799-.4-1.699-.601-2.6-.601-1 0-1.9.2-2.699.601-.701.399-1.4 1-1.801 1.8-.5.8-.699 1.7-.699 2.7s.299 1.899.699 2.699c.4.801 1 1.4 1.801 1.801.799.399 1.699.6 2.699.6.9 0 1.801-.2 2.6-.6.701-.4 1.4-1 1.801-1.801.5-.8.699-1.699.699-2.699S93.4 16.25 93 15.45zM90.1 19.85c-.4.4-.9.601-1.5.601s-1.1-.2-1.5-.601c-.4-.399-.6-1-.6-1.8s.199-1.399.6-1.8.801-.6 1.5-.6c.6 0 1.1.199 1.5.6s.6 1 .6 1.8S90.5 19.45 90.1 19.85zM101.699 13.05c-.799 0-1.4.2-2 .5s-1 .8-1.299 1.4v-1.8h-3v9.899h3v-5.3c0-.7.1-1.3.5-1.7.398-.399.898-.6 1.5-.6.6 0 1 .2 1.398.6.301.4.5.9.5 1.601v5.399h3v-5.8c0-1.3-.299-2.3-1-3.1C103.6 13.45 102.699 13.05 101.699 13.05L101.699 13.05zM113.5 17.65c-.5-.101-1.1-.301-1.9-.5-.699-.2-1.199-.301-1.5-.5-.301-.2-.5-.4-.5-.7 0-.2.1-.5.301-.601.199-.1.5-.199.799-.199.4 0 .801.1 1.1.3.301.3.5.6.602 1h2.898c-.199-1-.6-1.9-1.398-2.5-.701-.601-1.701-.9-3.201-.9-1.299 0-2.199.2-2.9.8-.699.601-1 1.301-1 2.2 0 .7.201 1.2.5 1.601C107.6 18.05 108 18.35 108.5 18.55s1.1.3 1.799.5c.701.2 1.301.4 1.602.5.299.101.5.4.5.7 0 .2-.102.4-.301.6-.199.101-.5.2-.801.2-.5 0-.898-.1-1.199-.3s-.5-.6-.5-1h-2.9c.201 1.1.701 1.9 1.5 2.5.801.6 1.9.9 3.201.9.799 0 1.5-.2 2.1-.4.5-.2 1-.6 1.299-1 .301-.4.5-.9.5-1.5 0-.7-.1-1.3-.5-1.7C114.4 18.15 114 17.85 113.5 17.65L113.5 17.65z"/></g></svg></div></a>
            </Link>
          </li>
         <li className="nav-search">  {this.props.page==='index' && <SearchForm/>}</li>
          <li className="nav-links">
         
          <Link href="/">
           <a>
           Icons</a>
            </Link>
            
          
            <Link href="/usage"><a>
           Usage</a>
            </Link>
           <a href="https://github.com/atisawd/boxicons" target="blank"><i className="bx bxl-github"/></a>
 

            
           
          </li>
  
          <li className="mobile-menu">
           <a href="#" className="mobile-menu-button" onClick={this.handleMenuClick}>
{this.state.showMobile ?
           <i className="bx bx-x"></i>:<i className="bx bx-menu-alt-left"></i>}
        
           </a>
      

{this.state.showMobile && 
             <ul className="mobile-menu-cont animated slideInRight" >
             
          <li className="mobile-link">
          <Link href="/" onClick={this.handleMenuLinkClick}>
           <a>
              Icons </a>
            </Link></li><li className="mobile-link">

         <a className="" href="https://gum.co/boxicons?wanted=true" target="_blank" data-gumroad-single-product="true">Download</a>
</li><li className="mobile-link">
            <Link href="/get-started" onClick={this.handleMenuLinkClick}>
           <a>
           Get Started </a>
            </Link>
           </li>
<li className="mobile-link">
<Link href="/cheatsheet" onClick={this.handleMenuLinkClick}>
           <a>
           Cheatsheet </a>
            </Link>
            </li>
           <li className="mobile-link">
       <a href="https://github.com/atisawd/boxicons" target="_blank">Github</a>
             
         
          </li></ul>}
         
          </li>

        </ul></div>
      </Navbar>
        );
    }
}
export default NavigationBar;